<template>
  <b-container>
    <b-form @submit="Login()">
      <b-row class="padding-top">
        <b-col md="4" />
        <b-col md="4">
          <b-form-input
            class="style-inputField-scheme"
            v-model="Username"
            type="text"
            placeholder="Username"
            autocomplete="off"
            required
          ></b-form-input>
        </b-col>
      </b-row>
      <b-row class="padding-top">
        <b-col md="4" />
        <b-col md="4">
          <b-form-input
            class="style-inputField-scheme"
            v-model="Password"
            type="password"
            placeholder="Password"
            autocomplete="off"
            required
          ></b-form-input>
        </b-col>
      </b-row>
      <b-row class="padding-top">
        <b-col md="4" />
        <b-col md="4">
          <b-button type="submit" class="style-button-scheme">Login</b-button>
        </b-col>
      </b-row>
    </b-form>
  </b-container>
</template>

<script>
import firebase from "firebase";

export default {
  name: "login",
  data() {
    return {
      Username: "",
      Password: ""
    };
  },
  created: function() {
    firebase.auth().onAuthStateChanged(
      user => {
        const isAuthed = Boolean(user);
        if (isAuthed) {
          this.$router.push("/dashboard");
        }
      },
      err => {
        console.log(err);
      }
    );
  },
  methods: {
    Login() {
      firebase
        .auth()
        .signInWithEmailAndPassword(this.Username, this.Password)
        .then(
          user => {
            return user;
          },
          err => {
            alert(err.message);
          }
        );
    }
  }
};
</script>
